.middle {
  background: linear-gradient(#e66465, #9198e5);
  border: 3px solid black;
  width: 100%;
  height: 50rem;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

